@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300&family=Playfair+Display:wght@700&family=Poppins:ital,wght@0,200;0,300;0,700;1,100;1,200;1,400&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* font-family: 'Mulish', sans-serif; */
/* font-family: 'Playfair Display', serif; */
/* font-family: 'Poppins', sans-serif; */

/* //navbar css */
.navbar{
  font-family:'Poppins', sans-serif;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 26px;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow:0 8px 6px -6px gray;
  border-bottom: solid gray !important;
}
.nav-link{
  font-weight: 300 !important;
}
.active{
  border-bottom: 2px solid black;
}
.navbar-brand{
  font-weight: 700;
  font-family: 'roboto',sans-serif;
  letter-spacing: 3px;
}

/* ----------Page not found CSS--------- */

.pnf{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf-title{
  font-size: 100px;
  font-weight: 700;
}
.pnf-heading{
  font-weight: normal;
}
.pnf-btn{
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover{
  background-color: black;
  color: #faebfa;
}


/* ----------Footer CSS--------- */
.footer{
  margin-top: 52px;
  color: white;
  padding: 25px;
  background: linear-gradient(to right, #434343,#000000);
}

.footer a{
  text-decoration: none;
  color: white;
  padding: 10px;
}
.footer a:hover{
  color: #faebfa;
  border-bottom: 1px solid #faebfa;
}



/**********product style ***********/
.product-Link{
  text-decoration: none !important;
  color: black !important;
}


