/*------------------==============
=======register page  */
.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    flex-direction: column;
    margin-top: 30px;  
}
.form-container form{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding:20px;
    background-color: #fff;
    width:40%
}
.form-container form .title{
    text-align: center;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 2rem;
    font-family: 'Playfair Display', serif;
}
.form-container form input{
    border:none;
    border-bottom: 1px solid #000;
    border-radius: 0;
}
.form-container form input:focus, .form-container form input:active{
   box-shadow: none !important;
   border-bottom: 1px solid #000 !important;
}
.form-container form button{
    border:1px solid black;
    border-radius: 0;
    background-color: #000;
    color:white;
    padding: 10px;
    margin-top: 20px;
}
.form-container form button:hover{
    border:1px solid black;
    background: #000000;
    background: -webkit-linear-gradient(to right,#434343,#000000); /*chrome 10-25 */
    background: linear-gradient(to right,#434343,#000000);
}

/*---------for max with 500------- */
@media only screen and (max-width:900px) {
    .form-container {
        min-height: 60vh;
        margin-top: 15px;
    }
    .form-container form{
        width:80% !important;
    } 
}
